import { DatePipe } from '@angular/common';
import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { EChartOption } from 'echarts';
import * as echarts from 'echarts';

@Component({
  selector: 'dhr-chart',
  templateUrl: './dhr-chart.component.html',
  styleUrls: ['./dhr-chart.component.css']
})
export class DhrChartComponent implements OnInit {

  @Input() chartData: any;
  @Input() xAxisLabel: any;
  @Input() yAxislabel: any;
  @Input() chartLabel: any;
  @Input() chartTitle: any;
  @Input() magicType: boolean;
  @Input() saveAsImage: boolean;
  @Input() isMultiSeries: boolean;
  @Input() showyAxisLabel: boolean;
  @Input() showToolbox: boolean;
  @Input() showToolTip: boolean;
  @Input() showxAxisLabel: boolean;
  @Input() isKpiOverView: boolean;
  @Input() color;

  chartOption: EChartOption;
  options: EChartOption;

  constructor() { }

  ngOnInit(): void {
    this.loadChart(this.chartData, this.xAxisLabel, this.yAxislabel, this.chartLabel, this.chartTitle,
       this.showyAxisLabel, this.showToolbox, this.showToolTip, this.showxAxisLabel, this.isKpiOverView, this.color);
  }
  ngOnChanges(changes: SimpleChanges): void {
    // Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    // Add '${implements OnChanges}' to the class.
    this.loadChart(this.chartData, this.xAxisLabel, this.yAxislabel, this.chartLabel, 
      this.chartTitle, this.showyAxisLabel, this.showToolbox, this.showToolTip, this.showxAxisLabel, this.isKpiOverView, this.color);
  }
  ngAfterViewInit(): void {
    // Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    // Add 'implements AfterViewInit' to the class.
    this.loadChart(this.chartData, this.xAxisLabel, this.yAxislabel, 
    this.chartLabel, this.chartTitle, this.showyAxisLabel, this.showToolbox, this.showToolTip,
     this.showxAxisLabel, this.isKpiOverView, this.color);
  }
  loadChart(d, xl, yl, cl, ct, yAxisLabelFlag, toolboxFlag, tooltipFlag, xAxisLabelFlag, kpiOverviewflag, colorValues) {
    if (this.isMultiSeries) {
      this.getMultiSeriesChartOptions(this.xAxisLabel, this.yAxislabel, this.chartData);
    } else {
      this.chartOption = this.getChartOptions(d, xl, yl, cl, ct,
      yAxisLabelFlag, toolboxFlag, tooltipFlag, xAxisLabelFlag, kpiOverviewflag, colorValues);
    }
  }

  getChartOptions(d, xl, yl, cl, ct, yAxisLabelFlag, toolboxFlag, tooltipFlag, xAxisLabelFlag, kpiOverviewflag, colorValues): any {
    let showMarkLine = {};
    let chartColorProperty = '#009DDC';
    if (!kpiOverviewflag){
      showMarkLine = {
        data: [
          {
            type: 'average',
            label: {
              position: 'End',
              distance: 5,
              formatter: (params) => {
                return `${params.value}`;
              },
            },
            lineStyle: { color: '#707070', width: 2 },
          },
        ],
        symbol: 'none',
      }
    }
    if (kpiOverviewflag) {
      chartColorProperty = colorValues;
    }
    return {
      title: {
        text: ct,
        left: 'left',
        top: 'left',
        triggerEvent: true,
        textStyle: {
          fontSize: 13,
        },
        padding: [
          15, // up
          10, // right
          5, // down
          10, // left
        ],
        show: false,
      },
      toolbox: {
        show: toolboxFlag,
        orient: 'vertical',
        showTitle: false,
        feature: {
          saveAsImage: {
            title: 'Save Image',
          },
        },
        tooltip: {
          show: true,
          formatter: (param) => {
            return '<div>' + param.title + '</div>';
          },
          backgroundColor: '#222',
          textStyle: {
            fontSize: 8,
          },
          extraCssText: 'box-shadow: 0 0 1px rgba(0, 0, 0, 0.3);', // user-defined CSS styles
        },
      },
      tooltip: {
        show: tooltipFlag,
        trigger: 'axis',
        backgroundColor: '#FFFFFF',
        axisPointer: {
          type: 'line',
          lable: {
            precision: 1,
          },
        },
        formatter: (params) => {
          return `<div style=" padding: 10px 10px 20px 10px;
          box-shadow: 2px 2px 4px 2px #aaaaaa;
          background-color: white;">
          <span style="color: #808080;font-size: 12px;">${
            new Date(params[0].name).toString().split(' ', 4).join(' ') +
            ' ' +
            ('0' + new Date(params[0].name).getHours()).slice(-2) +
            ':' +
            ('0' + new Date(params[0].name).getMinutes()).slice(-2)
            }</span><br>
                  <span style="color: #333333; font-size: 12px">${yl}</span>
                  &nbsp;&nbsp;&nbsp;
                  <span style="color: #333333; font-size: 14px; text-align:right; font-weight:700">${
            params[0].value[1]
            }&nbsp;${xl}</span></div>`;
        },
      },
      grid: {
        containLabel: true,
      },
      xAxis: {
        type: 'category',
        show: xAxisLabelFlag,
        axisLabel: {
          color: '#808080',
        },
        axisTick: {
          alignWithLabel: true,
        },
        splitNumber: 1,
      },
      yAxis: {
        type: 'value',
        name: yl,
        nameLocation: 'middle',
        show: yAxisLabelFlag,
        axisLabel: {
          color: '#808080',
        },
        nameTextStyle: {
          align: 'center',
          verticalAlign: 'bottom',
          lineHeight: 150,
          fontSize: 12,
          fontFamily: "Segoe UI",
          color: '#808080',
          fontWeight: 'normal',
        },
      },
      legend: {
        left: 'right',
        textStyle: {
          color: '#808080',
          fontSize: '10',
          fontWeight: 'bold',
          align: 'left',
        },
        padding: [
          10, // up
          70, // right
          0, // down
          0, // left
        ],
      },
      series: [
        {
          name: cl,
          smooth: false,
          symbol: 'circle',
          color: chartColorProperty,
          symbolSize: 2,
          data: d,
          type: 'line',
          lineStyle: {
            color: colorValues,
            width: 2,
          },
          markLine: showMarkLine,
        },
      ],
    };
  }

  getMultiSeriesChartOptions(xaxis, yaxis, data): void {
    const legends = [];
    const yAxisOptions = [];
    // const colors = [
    //   '#4F95D4',
    //   '#D53B8D',
    //   '#03B711'
    // ];
    const seriseData = [];
    yaxis.forEach((y, index) => {
      legends.push(y.name);
      legends.sort();
      yAxisOptions.push(this.generateYAxisOption(index, y));
      seriseData.push(
        this.generateSeriseData(index, y, data)
      );
    });
    this.chartOption = this.getMultiChartOptions(
      legends,
      xaxis,
      yAxisOptions,
      seriseData,
    );
  }

  generateYAxisOption(index, y): any {
    // const yAxisPosition = index % 2 !== 0 ? 'right' : 'left';
    // const yAxisColor = colors[index];
   // const nameTextPadding = yAxisPosition === 'left' ? [0, 2, 0, 0] : [0, 0, 0, 2]; // [top, right, bottom, left]
    const yOffset = Math.floor(index / 2) * 40;
    const yaxisoption = {
      type: 'value',
      name: y.name,
      nameLocation: 'center',
      nameGap: 65,
      nameTextStyle: {
        align: 'end',
        // verticalAlign: 'bottom',
        fontSize: 12,
        fontFamily: "Segoe UI",
        color: '#808080',
        fontWeight: 'normal',
      },
      min: y.min,
      max: y.max,
      padding: [3, 0, 0, 0],
     // position: yAxisPosition,
      axisLine: {
        onZero: true,
        lineStyle: {
          // color: yAxisColor,
          width: 2
        }
      },
      offset: yOffset,
      axisLabel: {
        show: true,
        formatter: '{value}'
      },
    };
    return yaxisoption;
  }

  generateSeriseData(index, y, data): any {
    const seriseObj = {
      name: y.name,
      type: 'line',
      yAxisIndex: index,
      lineStyle: {
        //color: colors[index],
        width: 2
      },
      markLine: {
        symbol: 'none'
      },
      data: data[index]
    };
    return seriseObj;
  }

  getMultiChartOptions(legends, xaxis, yaxis, seriseData): any {
    return {
      //colors: colors,
      tooltip: {
        trigger: 'axis',
        backgroundColor: '#FFFFFF',
        textStyle: {
          color: '#808080',
          fontSize: '10',
          fontWeight: 'bold',
          align: 'left',
        },
        axisPointer: {
          type: 'cross',
          lable: {
            precision: 1,
          },
        }
      },
      toolbox: {
        feature: {
          dataZoom: {
            yAxisIndex: 'none',
            title : {
              zoom : 'Zoom',
              back : 'Zoom Reset'
            }
          },
          dataView: {
            readOnly: true,
            title: 'Data View',
            lang : ['Data View', 'Close']
          },
          magicType: { show: false, type: ['line'] },
          restore: {
            title: 'Restore'
          },
          saveAsImage: {
            title: 'Save Image',
          },
        }
      },

      legend:
      {
        left: 'right',
        textStyle: {
          color: '#808080',
          fontSize: '10',
          fontWeight: 'bold',
        },
        padding: [10, 140, 0, 0],
        data: legends.sort(),
        icon: 'circle',
      },
      xAxis: [
        {
          type: 'category',
          data: xaxis
        }
      ],
      yAxis: yaxis,
      series: seriseData
    };
  }

  formatTime(value): any {
    const datePipe = new DatePipe('en-US');
    const tempDate =
      datePipe.transform(new Date(value), 'dd/MM/yyyy') +
      '\n' +
      datePipe.transform(new Date(value), 'shortTime');
    return tempDate;
  }
}